
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Messenger  from '../home/Messenger';
import Feedback from '../home/Feedback';

import { Fragment } from 'react';
class privacypolicy extends Component {
  render() {

return (
    <Fragment>
    <div className="section">
        <h1 className='text-center legeal-title'> Privacy Policy </h1>
        <div className='container'>
            <div className='terms-p' >
                <h5><u>Our Privacy Policy</u></h5>
                <p>This Privacy Policy (“Policy”) describes how Tenali Double Horse Movement Co. and its affiliates (“Tenali Double Horse” or “we” or “our”) treat information collected or provided in connection with an end user’s (“you” or “your” or “user”) use of the Tenali Double Horse website (the “Site”) and all services provided by Tenali Double Horse in connection with the Site (such services together with the Site, the “Services”) including without limitation our environmental air monitoring services and technologies. Please note that your use of the Services constitutes your acceptance of this Policy as set forth below.
                </p>
            </div>
            <div className='terms-p' >
                <h5><u>Effective Date Changes</u></h5>
                <p>This Policy applies to all information collected by or provided to us in connection with the Services. Tenali Double Horse may make changes to the Site in the future and as a consequence will need to revise this Policy to reflect those changes. Tenali Double Horse will post all such changes on the Site, so you should review this page periodically. If we make a material change to the Policy, you will be provided with appropriate notice.
                </p>
            </div>
            <div className='terms-p' >
                <h5><u>Information You Submit</u></h5>
                <p>When you log-in to or create an account with the Services, we collect the information you provide. This information may include personally identifiable information such as your name, mailing address, e-mail address and password. We may also collect your job title and information about the business or organization that you are associated with.  To the extent you decline to share certain personally identifiable information with us, we may not be able to provide some or all of the features and functionalities found on the Services.
                </p>
            </div>
            <div className='terms-p' >
                <h5><u>Information Collected Automatically</u></h5>
                <p>When you use the Services, we automatically gather certain non-personally identifiable information from you. This includes usage information, such as information on when, how often and for how long you use the Services and the content you submit and view. This also includes server log data such as the IP address, operating system, or the webpage you were visiting before you came to our Site. As described below, we also collect this information through a number of other means, including cookies, software development kits (“SDKs”) and other online tracking mechanisms. We may associate such non-personally identifiable information with the personally identifiable information that you provide in connection with the Services, but in those situations, we will treat that information consistently with the terms of this Policy.
                </p>
                <p>We also use cookies on the Site. Cookies are small files that are stored on your computer by your web browser. A cookie allows the Site to recognize whether you have visited before and may store user preferences and other information. For example, cookies can be used to collector store information about your use of the Site during your current session and over time (including the pages you view and the files you download), your computer’s operating system and browser type, your Internet service provider, your domain name and IP address, your general geographic location, the website that you visited before the Site, and the link you used to leave the Site. If you are concerned about having cookies on your computer, you can set your browser to refuse all cookies or to indicate when a cookie is being set, allowing you to decide whether to accept it. You can also delete cookies from your computer. However, if you choose to block or delete cookies, certain features of the Site may not operate correctly.</p>
            </div>
            <div className='terms-p' >
                <h5><u>Analytics Sites</u></h5>
                <p>We use certain third-party services to assist us in analysis, auditing, research, and reporting regarding the Services. These third parties may use web logs or web beacons, and they may set and access cookies on your computer or other device. In particular, we use Google Analytics to help collect and analyze certain information for the purposes discussed in this Policy. For information about the privacy practices of Google Analytics, you can visit google.com/policies/privacy/partners/. You can learn how to opt out of the use of cookies by Google Analytics at https://tools.google.com/dlpage/gaoptout.
                </p>
            </div>
            <div className='terms-p' >
                <h5><u>Transactions</u></h5>
                <p>If you participate in a transaction through the Services, such as making a payment through the Services for a product or setting up a recurring payment for our subscription services, our third-party payment processor will receive your payment information for purposes of processing such transaction and Tenali Double Horse will receive such payment after any applicable fees have been deducted in accordance with Tenali Double Horse agreement with such third party payment processor. Note that Tenali Double Horse does not receive your credit card or bank information, and payment card information you provide through our Site is governed by the privacy policy and terms of service of our third-party payment processor.
                </p>
            </div>
            <div className='terms-p' >
                <h5><u>Use and Processing of Information</u></h5>
                <p>We use the information that we collect for a variety of purposes.  Our legal bases for processing your personal information are:</p>
                  <ul className='prives-li'>
                      <li>our legitimate interest in running and maintaining our business;</li>
                      <li>performance and fulfillment of our contracts;</li>
                      <li> compliance with our legal obligations. In many instances, more than one of these legal bases apply to the processing of your personal information.</li>
                  </ul>
                  <p><b>The purposes for which we may use your information include:</b></p>
                  <div className='privicey-second-sec'>
                    <p>We will use submitted information for the purposes for which you provided the information, including, for example, to create and     maintain a Services account for you or respond to a question that you e-mail to us or submit through our customer service chat functionality. We also use submitted information and collected information as necessary to provide the features and functionality of the Services to you.</p>
                    <p>We may send you e-mails regarding updates or modifications to the Services.</p>
                    <p>We may analyze collected information relating to your use of the Services in order to help us improve the Services and develop and improve other products and services.</p>
                    <p>We may use submitted information and collected information to help troubleshoot problems, provide you with required notices, enforce our Terms and Conditions of Service or to alert you to changes in our policies or agreements that may affect your use of the Services.</p>
                    <p>We may use the information we collect automatically for interest-based advertisements, as described below.</p>
                    <p>We may combine personally identifiable information collected through the Services with other information that we collect about you in other contexts—such as our communications with you via e-mail, phone or postal mail. We will handle such combined information in  accordance with this Policy.</p>
                    <div className='terms-p' >
                        <h5><u>Security</u></h5>
                        <p>The personally identifiable information we collect about you is stored in limited access servers. We will maintain reasonable safeguards to protect the security of these servers and your personally identifiable information. However, no security measures are 100% effective and we cannot guarantee the security of your personally identifiable information.
                        </p>
                    </div>
                    <div className='terms-p' >
                        <h5><u>Data Retention</u></h5>
                        <p>We retain personal information about you necessary to fulfill the purpose for which that information was collected or as required or permitted by law. We do not retain personal information longer than is necessary for us to achieve the purposes for which we collected it. When we destroy your personal information, we do so in a way that prevents that information from being restored or reconstructed.
                        </p>
                    </div>
                    <div className='terms-p' >
                        <h5><u>Contact Us</u></h5>
                        <p>If you have any question regarding our privacy policy, please email <a href="mailto:Info@tenalidoublehorse.com">Info@tenalidoublehorse.com</a>
                        </p>
                    </div>
                  </div>
                   
            </div>
        </div>
    </div>
 <Messenger/>
 <Feedback/>
  </Fragment>
);
}
}

export default privacypolicy;
